*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb {
  background-color: yellow;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.App {
  text-align: center;
  font-size: 10px;
  background-color: #322f2f;
}

@use "./variables";
.movie-card {
  transition: all 0.3s;
  position: relative;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  width: 300px;
  display: block;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-family: "Bai Jamjuree", sans-serif;
}

.movie-card .movie-card__container-image {
  position: relative;
  max-width: 100%;
  height: 350px;
  overflow: hidden;
}

.movie-card .movie-card__container-image .movie__image {
  object-position: 50% 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-card .movie-card__container-image .movie__summary {
  position: absolute;
  padding: 1em;
  background-color: #ffffff;
  color: #322f2f;
  bottom: 0;
  opacity: 0;
  max-height: 100%;
  left: 0;
  right: 0;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  font-size: 1.4em;
}

.movie-card .movie__info {
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: 0.5fr (1fr)[2];
      grid-template-rows: 0.5fr repeat(2, 1fr);
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
}

.movie-card .movie__info .movie__title {
  border-bottom: 1px solid;
  border-color: rgba(0, 0, 0, 0.3);
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-column: 1/4;
}

.movie__info :first-child {
  font-size: 1.4em;
}

.movie__info .movie__info-type {
  font-size: 1.5em;
  font-weight: bold;
}

.movie__info .bottom {
  border-bottom: 1px solid;
  border-color: rgba(0, 0, 0, 0.3);
}

.movie__info .right {
  border-right: 1px solid;
  border-color: rgba(0, 0, 0, 0.3);
}

.movie__info .movie__follow {
  cursor: pointer;
}

.movie-card__container-image:hover .movie__summary {
  z-index: 1;
  opacity: 1;
  overflow-y: auto;
  transform: translateY(0%);
}

.movie-card__container-image:hover .movie__image {
  z-index: -1;
  filter: blur(2px);
}

.movie__summary::-webkit-scrollbar {
  width: 7px;
  background-color: #ffffff;
}

.movie__summary::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.movie__summary::-webkit-scrollbar-thumb {
  background-color: #322f2f;
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}
