.search {
  margin: 0.5em 0;
}

.search .search-button {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
  border: 1px solid #e7ff2c;
  padding: 0.3em;
  border-radius: 7px;
  background-color: #322f2f;
  color: #ffffff;
  transition: 0.3s;
  margin-left: 3px;
  margin-top: 5px;
}

.search .search-bar {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 10px;
  margin-right: 2px;
  bottom: 1px solid;
  padding: 0.2em 0.3em;
  border: 1px solid #e7ff2c;
}

.search-bar:focus {
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(242, 242, 245, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  outline: none;
  color: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.search-button:hover {
  cursor: pointer;
  background-color: #e7ff2c;
  color: #322f2f;
}

.search-button:focus {
  outline: none;
}
