.main {
  position: relative;
  font-size: 10px;
}

.nav-bar {
  top: 0;
  width: 100%;
  background-color: #322f2f;
  z-index: 99;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: space-between;
  padding: 0.7rem 15rem;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  font-size: 1.5em;
  font-family: "Bai Jamjuree", sans-serif;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

@media only screen and (max-width: 687px) {
  .nav-bar {
    justify-content: space-evenly;
  }
}

.nav-bar .nav-bar__logo {
  transition: all 0.3s;
  color: #e7ff2c;
  font-weight: 700;
  font-size: 1.7em;
  font-family: "Frijole", cursive;
  cursor: pointer;
}

.nav-bar .nav-bar__logo .fa-television {
  animation: flicker 1s ease-in-out infinite alternate;
}

.nav-bar .navbar-bar__content {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  align-items: center;
}

@media only screen and (max-width: 687px) {
  .nav-bar .navbar-bar__content {
    flex-wrap: wrap-reverse;
    justify-content: center;
  }
}

.nav-bar__save {
  color: #ffffff;
  font-weight: 700;
  padding: 3px;
  cursor: pointer;
  border-radius: 5px;
}

.nav-bar__save:hover {
  color: #e7ff2c;
}

.nav-bar__list {
  font-weight: 700;
  padding: 3px;
  cursor: pointer;
  border-radius: 5px;
  color: #ffffff;
}

.nav-bar__list:hover {
  color: #e7ff2c;
}

.section-title {
  color: #ffffff;
  font-family: "Audiowide", cursive;
  font-size: 3em;
  border-bottom: 1px solid;
  padding-bottom: 20px;
}

.main-movies {
  -ms-grid-column-align: center;
      justify-self: center;
  -ms-grid-columns: (300px)[auto-fit];
      grid-template-columns: repeat(auto-fit, 300px);
  width: 90vw;
  margin: 2em auto;
  display: -ms-grid;
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
}

.premiered-header {
  background-color: #322f2f;
  position: -webkit-sticky;
  position: sticky;
  z-index: 99;
}

.premiered-movies {
  padding: 10px 0 0 0;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 850px;
}

.premiered-movies .movie-card {
  overflow: hidden;
  width: 2vw;
  box-shadow: 0 0 25px 10px rgba(0, 0, 0, 0.5);
  float: left;
  border-left: 1px solid #888;
  display: block;
  transition: all 0.5s;
}

.premiered-movies .movie-card .movie-card__container-image {
  height: 650px;
}

.premiered-movies:hover .movie-card {
  width: 1vw;
}

.premiered-movies .movie-card:hover {
  width: 400px;
}

.shrink {
  transition: all 0.3s;
  padding: 0.5rem 15rem;
}

.shrink .nav-bar__logo {
  font-size: 1.3em;
}

@keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #e7ff2c, 0 0 80px #e7ff2c, 0 0 90px #e7ff2c, 0 0 100px #e7ff2c, 0 0 150px #e7ff2c;
  }
  20%,
  24%,
  55% {
    text-shadow: none;
  }
}

@media screen and (max-width: 939px) {
  .premiered-movies {
    height: 850px;
  }
  .premiered-movies .movie-card {
    width: 3vw;
  }
  .premiered-movies:hover .movie-card {
    width: 2vw;
  }
  .premiered-movies .movie-card .movie-card__container-image {
    height: 500px;
  }
  .premiered-movies .movie-card:hover {
    width: 300px;
  }
}
