.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: white;
  background-color: rgba(128, 128, 128, 0.5);
  z-index: 98;
}

.movie-page {
  position: fixed;
  font-size: 14px;
  text-align: left;
  font-family: "Bai Jamjuree", sans-serif;
  color: #ffffff;
  z-index: 98;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transform: translate(-50%, -50%);
  left: calc(50vw - 450px);
  top: 20vh;
}

.movie-page__container {
  position: fixed;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 350px 550px;
      grid-template-columns: 350px 550px;
  -ms-grid-rows: 1fr;
      grid-template-rows: 1fr;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px;
  background-color: #322f2f;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.movie-page__image-container {
  width: 350px;
  height: 100%;
  background-color: #e7ff2c;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  overflow: hidden;
}

.movie-page__image-container .movie__image {
  height: auto;
  width: 100%;
  filter: opacity(0.7);
}

.movie-page__content {
  padding: 15px;
}

.movie-title {
  text-align: left;
  font-family: "Bai Jamjuree", sans-serif;
  padding: 10px 0;
}

.movie-page__genre {
  padding: 10px 0;
}

.movie-page__genre ul {
  text-align: left;
  list-style: none;
  display: flex;
  flex-direction: right;
}

.movie-page__rating {
  color: #e7ff2c;
  padding: 10px 0;
}

.movie-page__lang {
  padding: 10px 0;
}

.movie-page__summary {
  text-align: left;
  padding: 10px 0;
  height: 30%;
  max-height: 31%;
  overflow-y: auto;
}

.movie-page__summary::-webkit-scrollbar {
  width: 12px;
}

.movie-page__summary::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.movie-page__summary::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

.movie-page__buttons {
  display: flex;
  flex-direction: right;
  margin: 8%;
  justify-content: space-around;
}

.movie-page__buttons .movie-page__site {
  cursor: pointer;
}

.movie-page__buttons .movie-page__site button {
  font-size: 22px;
  cursor: pointer;
  padding: 3px;
  border-radius: 5px;
  line-height: 1.8rem;
  display: block;
  border: 2px solid #e7ff2c;
  width: 165px;
  background-image: linear-gradient(to right, #e7ff2c 50%, #322f2f 50%);
  color: #e7ff2c;
  text-align: center;
  background-position: 100% 0%;
  background-size: 200% 100%;
  transition: 0.5s;
}

.movie-page__buttons .movie-page__site button:hover {
  background-position: 0% 100%;
  color: #322f2f;
}

.movie-page__buttons .movie-page__site button:focus {
  outline: none;
}

.movie-page__buttons .movie-page__follow {
  font-size: 22px;
  cursor: pointer;
  padding: 3px;
  border-radius: 5px;
  line-height: 1.8rem;
  display: block;
  border: 2px solid #e7ff2c;
  width: 165px;
  background-image: linear-gradient(to right, #e7ff2c 50%, #322f2f 50%);
  color: #e7ff2c;
  text-align: center;
  background-position: 100% 0%;
  background-size: 200% 100%;
  transition: 0.5s;
}

.movie-page__buttons .movie-page__follow:hover {
  background-position: 0% 100%;
  color: #322f2f;
}

.movie-page__buttons .movie-page__follow:focus {
  outline: none;
}

.movie-page-close {
  text-align: right;
}

.movie-page-close i {
  font-size: 20px;
  text-align: right;
  cursor: pointer;
  border: none;
  background: none;
  color: #ffffff;
}

.movie-page-close i:hover {
  transform: rotate(25deg) scale(1.3);
  color: red;
}

.movie-page-close i:focus {
  outline: none;
  transform: rotate(60deg) scale(1.3);
  color: #e7ff2c;
}

@media screen and (max-width: 915px) {
  .movie-page {
    font-size: calc(10px + (14 - 9) * ((100vw - 300px) / (1600 - 300)));
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transform: translate(-50%, -50%);
    left: 5vw;
    top: 30vh;
  }
  .movie-page__container {
    position: fixed;
    -ms-grid-columns: 45vw 45vw;
        grid-template-columns: 45vw 45vw;
